import React,{Component} from 'react';
import {NavLink} from 'react-router-dom';

class FooterList extends Component{
    render(){
        let {name,classChild,hash} = this.props;
        // 'zAction_a zAction_color'
        // activeStyle={{color: '#0089dc'}}
        return(
            <NavLink to={hash} className="zAction_a"
                exact
                activeClassName="zAction_color"
            >
                <i className={classChild}></i>
                <span>{name}</span>
            </NavLink>
        )
    }
}
export default FooterList;



