import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import '../../css/seachStyle.css';

class Search extends Component{
    render(){
        $('body').css('background','#fff')
        $('title').html('搜索')
        return(
            <div>
                <div className="search">
                    <Link className="icon-direction_l" to='/'></Link>
                    <input type="text" placeholder="请输入商品名称"/>
                </div>
                <section>
                    <header className="history">
                        <span>历史搜索</span>
                        <i className="icon-del"></i>
                    </header>
                    <section className="item_search">
                        <span>烤肉</span>
                        <span>焖面</span>
                        <span>烤翅</span>
                        <span>炸鸡</span>
                        <span>凉皮</span>
                    </section>
                </section>
                <section>
                    <header className="history">
                        <span>热门搜索</span>
                    </header>
                    <section className="item_search">
                        <span>烤肉</span>
                        <span>焖面</span>
                        <span>烤翅</span>
                        <span>炸鸡</span>
                        <span>凉皮</span>
                    </section>
                </section>
            </div>
        )
    }
}
export default Search;



