import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import '../../css/userStyle.css';
import '../../css/discoverStyle.css';
import Footer from '../footer/footer';

class User extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('我的')
        console.log(localStorage.getItem('user'))
        // let n = localStorage.getItem('user');
        // console.log(JSON.parse(n).succeed)
        let html = null;
        if(localStorage.getItem('user')){
            html = (
                <Link to='/user/info' className="userName">
                    <img src="https://i.loli.net/2017/08/26/59a04aad94da4.jpg" alt="用户"/>
                    <div className="userTxt">
                        <p className="userp1">拼点爱给我</p>
                        <p className="userp2">
                            <i className="icon-mobile"></i>
                            <span>138****7714</span>
                        </p>
                    </div>
                    <i className="icon-direction_r"></i>
                </Link>
            )
        }else{
            html = (
                <Link to='/login' className="userName">
                    <span className="userImg">
                        <i className="icon-headshot"></i>
                    </span>
                    <div className="userTxt">
                        <p className="userp1">登录/注册</p>
                        <p className="userp2">
                            <i className="icon-mobile"></i>
                            <span>登录后享受更多特权</span>
                        </p>
                    </div>
                    <i className="icon-direction_r"></i>
                </Link>
            )
        }
        return(
            <div>
                <div className="Header">
                    <div className="orderTxT">
                        {/* <i className="icon-direction_l"></i> */}
                        <div>我的</div>
                    </div>
                </div>
                <div className="zyhuser">
                    {/* <Link to='/login' className="userName">
                        <img src="https://i.loli.net/2017/08/26/59a04aad94da4.jpg" alt="用户"/>
                        <div className="userTxt">
                            <p className="userp1">拼点爱给我</p>
                            <p className="userp2">
                                <i className="icon-mobile"></i>
                                <span>138****7714</span>
                            </p>
                        </div>
                        <i className="icon-direction_r"></i>
                    </Link> */}
                    {html}
                </div>
                <div className="discounts">
                    <Link to='/user/discounts' className="leftA">
                        <p>
                            <span className="disSpan">0</span>
                            <span>个</span>
                        </p>
                        <p>红包</p>
                    </Link>
                    <Link to='/user/points'>
                        <p>
                            <span className="disSpan disSpan2">0</span>
                            <span>分</span>
                        </p>
                        <p>积分</p>
                    </Link>
                </div>
                <section className="con_item">
                    <Link to='/order'>
                        <i className="icon-order item-icon"></i>
                        <div className="itemTxt">
                            <span>我的订单</span>
                            <i className="icon-direction_r"></i>
                        </div>
                    </Link>
                    <Link to='/user/shopping'>
                        <i className="icon-shop item-icon"></i>
                        <div className="itemTxt borNone">
                            <span>积分商城</span>
                            <i className="icon-direction_r"></i>
                        </div>
                    </Link>
                </section>
                <section className="con_item">
                    <Link to='/user/service'>
                        <i className="icon-serve item-icon"></i>
                        <div className="itemTxt">
                            <span>服务中心</span>
                            <i className="icon-direction_r"></i>
                        </div>
                    </Link>
                    <Link to='/user/download'>
                        <i className="icon-eleme item-icon"></i>
                        <div className="itemTxt borNone">
                            <span>下载饿了么APP</span>
                            <i className="icon-direction_r"></i>
                        </div>
                    </Link>
                </section>
                <Footer/>
            </div>
        )
    }
}
export default User;



