import React,{Component} from 'react';
import $ from 'jquery';
import '../../css/orderStyle.css';
import '../../css/discoverStyle.css';
import Footer from '../footer/footer';

class Order extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('订单')
        return(
            <div>
                <div className="Header">
                    <div className="orderTxT">
                        {/* <i className="icon-direction_l"></i> */}
                        <div>订单</div>
                    </div>
                </div>
                <section className="no_login">
                    <img src="https://i.loli.net/2017/08/26/59a04aa31112f.gif" alt="登录"/>
                    <h3>登录后查看外卖订单</h3>
                    <button>立即登录</button>
                </section>
                <div className="sum">
                    <a className="sumItem">
                        <div className="order_item">
                            <div className="order_left">
                                <img src="https://i.loli.net/2017/08/26/59a04aad7932e.jpg" alt="商户"/>
                            </div>
                            <div className="order_right">
                                <div className="order_right_1">
                                    <div className="order_name">
                                        <p className="name_1">
                                            <span>张姐烤肉拌饭</span>
                                            <i className="icon-direction_r"></i>
                                        </p>
                                        <p className="order_over">订单已完成</p>
                                    </div>
                                    <p className="order_time">2小时01分钟前</p>
                                </div>
                                <div className="order_right_2">
                                    <span className="span1">原味烤肉拌饭+可乐+咸鸭蛋</span>
                                    <span className="span2">¥9.00</span>
                                </div>
                            </div>
                        </div>
                        <div className="integral">
                            <button>再来一单</button>
                            <button className="num">评价得100积分</button>
                        </div>
                    </a>
                </div>
                <Footer/>
            </div>
        )
    }
}
export default Order;



