import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './downStyle.css';

class Download extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('下载APP')
        return(
            <div>
                <div className="Header">
                    <div className="downTxT">
                        <Link to='/user' className="icon-direction_l"></Link>
                        <div>下载APP</div>
                    </div>
                </div>
                <section className="downApk">
                    <div className="downImg">
                        <img src="https://i.loli.net/2017/09/02/59aabda81b087.png" alt="下载"/>
                        <span>扫码下载APP</span>
                    </div>
                </section>
            </div>
        )
    }
}
export default Download;



