import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './infoStyle.css';

class Info extends Component{
    constructor(){
        super()
        this.state = {
            mask:false,
            txt:''
        }
    }
    site =()=>{
        this.setState({
            mask:true,
            txt:'请在手机APP上设置地址信息'
        }) 
    }
    mobile =()=>{
        this.setState({
            mask:true,
            txt:'请在手机APP上设置绑定信息'
        })
    }
    password =()=>{
        this.setState({
            mask:true,
            txt:'抱歉，暂不支持密码修改！'
        })
    }
    noneMask =()=>{
        this.setState({
            mask:false
        })
    }
    // escLogin =(ev)=>{
    //     document.addEventListener('touchstart',function(ev){
    //         ev.preventDefault()
    //     })
    //     ev.ev.stopPropagation()
    // }
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('用户信息')
        let {mask} = this.state;
        let {txt} = this.state;
        let mackShow = mask?'mask maskShow':'mask';
        return(
            <div>
                <div className="Header">
                    <div className="loginTxT">
                        <Link to='/user' className="icon-direction_l"></Link>
                        <div>用户信息</div>
                    </div>
                </div>
                <section className="userInfo">
                    <section className="infoItem">
                        <span className="userList">头像</span>
                        <div className="rightInfo">
                            <img src="https://i.loli.net/2017/08/26/59a04aad94da4.jpg" alt="user"/>
                            <i className="icon-direction_r"></i>
                        </div>
                    </section>
                    <section className="infoItem">
                        <span className="userList">用户名</span>
                        <div className="rightInfo">
                            <span className="listTxt">138****7714</span>
                            <i className="icon-direction_r"></i>
                        </div>
                    </section>
                    <section className="infoItem borNone"
                        onClick={this.site}
                    >
                        <span className="userList">收货地址</span>
                        <div className="rightInfo">
                            <span className="listTxt"></span>
                            <i className="icon-direction_r"></i>
                        </div>
                    </section>
                </section>
                <h3 className="infoText">账号绑定</h3>
                <section className="infoItem borNone"
                    onClick={this.mobile}
                >
                    <span className="userList">手机</span>
                    <div className="rightInfo">
                        <span className="listTxt"></span>
                        <i className="icon-direction_r"></i>
                    </div>
                </section>
                <h3 className="infoText">安全设置</h3>
                <section className="infoItem borNone"
                    onClick={this.password}
                >
                    <span className="userList">登录密码</span>
                    <div className="rightInfo">
                        <span className="listTxt">修改</span>
                        <i className="icon-direction_r"></i>
                    </div>
                </section>
                <Link to='/user' 
                    className="escLogin"
                    //onClick={this.escLogin}
                >退出登录</Link>
                <div className={mackShow}>
                    <div className="maskCon">
                        <div className="circle">
                            <span className="circleS1"></span>
                            <span className="circleS2"></span>
                        </div>
                        <p className="tips">{txt}</p>
                        <div className="maskDel"
                            onClick={this.noneMask}
                        >确认</div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Info;



