import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './questionStyle.css';

class Member extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('会员问题')
        return(
            <div>
                <div className="Header">
                    <div className="timeTxT">
                        <Link className="icon-direction_l" to='/user/service'></Link>
                        <div>会员问题</div>
                    </div>
                </div>
                <div className="question">
                    <h3>Q1: 超级会员是否有配送费优惠？</h3>
                    <ul>
                        <li>超级会员暂无配送费优惠政策，配送费按商家定价收取。</li>
                    </ul>
                    <h3>Q2: 为何没收到会员专享红包？</h3>
                    <ul>
                        <li>专享红包按月发放，如用户购买多月会员，则每月会员生效日可获得对应的4个专享红包。</li>
                    </ul>
                    <h3>Q3: 如何获得下单奖励红包？</h3>
                    <ul>
                        <li>会员有效期内，用户每完成5笔在线支付订单（订单实际支付金额需≥20元），即可去会员主页领取5元红包，如会员已过期，则无法领取。</li>
                    </ul>
                    <h3>Q4: 超级会员如何退订？</h3>
                    <ul>
                        <li>会员到期后，如用户不再续费则自动退订，会员有效期内无法退订会员，已缴纳的会费不能返还。</li>
                    </ul>
                    <h3>Q5:红包有无使用限制？</h3>
                    <ul>
                        <li>专享红包及下单奖励红包无金额门槛要求，但每笔订单需最少支付0.01元，如抵扣金额小于红包金额，则需支付0.01元；</li>
                        <li>每个红包只能使用一次，不能叠加或拆分使用；</li>
                        <li>红包需在有效期内使用，过期红包即刻失效，请注意红包有效期；</li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default Member;



