import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import '../../css/indexStyle.css';
import Footer from '../footer/footer';
import Slider from './slider';
import Data from '../data/data';
import Merchant from './merchant';

class Home extends Component{
    constructor(){
        super();
        this.state = {
            data:Data
        }
    }
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('饿了么')
        let {data} = this.state;
        let merchantList = null;
        merchantList = data.map((e,i)=>{
            let data = {
                key:i,
                // 店铺名
                name:e.seller.name,
                // 图片
                avatar:e.seller.avatar,
                // 配送
                description:e.seller.description,
                // 时间
                deliveryTime:e.seller.deliveryTime,
                // 评分
                score:e.seller.score,
                // 小星星
                percentage:Math.round(e.seller.score/5*100),
                // 销量
                sellCount:e.seller.sellCount,
                // 起送
                minPrice:e.seller.minPrice,
                // 人均
                ratingCount:e.seller.ratingCount,
                // 配送费
                deliveryPrice:e.seller.deliveryPrice,
                // 距离
                distance:e.seller.distance,
                // 活动详情
                supports:e.seller.supports,
                // 活动数量
                num:e.seller.supports.length,
            }
            return <Merchant {...data}/>
        })
        return(
            <div>
                <div className="zCon">
                    <div className="zHeader">
                        <div className="zSeat">
                            <Link className="zLocate" to='/location'>
                                <i className="icon-location zIcon"></i>
                                <span className="zPlace">您的位置</span>
                                <i className="icon-triangle zIcon"></i>
                            </Link>
                            <Link className="zLogin" to='/login'>
                                <span>登录 | 注册</span>
                            </Link>
                        </div>
                        <Link to='/search'>
                            <input type="text" className="zText" placeholder="搜索商家、商品" />
                        </Link>
                    </div>
                    <div className="content">
                        <Slider/>
                        <h3 className="zShops">推荐商家</h3>
                        <ul className="zSection">
                            {/* <li>
                                <div className="li_left">
                                    <div className="shop_log">
                                        <img src="https://i.loli.net/2017/08/26/59a04aad7932e.jpg" alt="商户"/>
                                    </div>
                                </div>
                                <div className="li_right">
                                    <div className="zItem">
                                        <div className="username">
                                            <span>张姐烤肉拌饭</span>
                                        </div>
                                        <div className="bill">
                                            <span>保</span>
                                            <span>票</span>
                                        </div>
                                    </div>
                                    <div className="zItem zItem_mt">
                                        <div className="shop_star">
                                            <div className="star">
                                                <div className="gray">
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                </div>
                                                <div className="yellow">
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                </div>
                                            </div>
                                            <span className="grade">4.7</span>
                                            <span>月售100单</span>
                                        </div>
                                        <div className="distribution">
                                            <span>蜂鸟专送</span>
                                        </div>
                                    </div>
                                    <div className="zItem zItem_mt">
                                        <div className="delivery">
                                            <span>¥20起送</span>
                                            <i>/</i>
                                            <span>配送费¥5</span>
                                            <i>/</i>
                                            <span>¥15/人</span>
                                        </div>
                                        <div className="zTime">
                                            <span>1.10km</span>
                                            <i>/</i>
                                            <span className="blue">20分钟</span>
                                        </div>
                                    </div>
                                    <div className="discount">
                                        <div className="zList">
                                            <div className="zDis">
                                                <i className="activity_1">新</i>
                                                <span className="activity">
                                                    <span>新用户下单立减10元</span>
                                                </span>
                                            </div>
                                            <div className="zDis">
                                                <i className="activity_2">减</i>
                                                <span className="activity">
                                                    <span>满20减10，满30减18，满50减30</span>
                                                </span>
                                            </div>
                                            <div className="zDis zHid">
                                                <i className="activity_2">折</i>
                                                <span className="activity">
                                                    <span>米饭类5折</span>
                                                </span>
                                            </div>
                                            <div className="zDis zHid">
                                                <i className="activity_3">赠</i>
                                                <span className="activity">
                                                    <span>满100.0元赠送鸡排1份</span>
                                                </span>
                                            </div>
                                            <div className="zDis zHid">
                                                <i className="activity_4">特</i>
                                                <span className="activity">
                                                    <span>新品鸡肉卷特惠价</span>
                                                </span>
                                            </div>
                                        </div>
                                        <div className="count">
                                            <span>5个活动</span>
                                            <span className="zDown">
                                                <i className="icon-triangle"></i>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </li> */}
                            {merchantList}
                        </ul>
                    </div>
                </div>
                <Footer/>
            </div>
        )
    }
}
export default Home;



