import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './questionStyle.css';

class Balance extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('余额问题')
        return(
            <div>
                <div className="Header">
                    <div className="timeTxT">
                        <Link className="icon-direction_l" to='/user/service'></Link>
                        <div>余额问题</div>
                    </div>
                </div>
                <div className="question">
                    <h3>Q1: 使用余额的条件？</h3>
                    <p>为了保护账户安全，使用余额前必须先绑定手机号。</p>
                    <h3>Q2: 余额可以怎么用？</h3>
                    <p>余额可以饿了么外卖平台上消费（当余额大于等于待支付金额时可使用余额支付）。</p>
                    <h3>Q3:余额提现有什么规则？</h3>
                    <p>为了保护账户和资金安全，每个用户（拥有相同手机号、支付账号、饿了么账号与硬件设备的用户，均视为同一个用户），单日仅可提现一次，提现最高限额为50元。</p>
                    <h3>Q4:提现到账支持哪些银行？</h3>
                    <p>目前在银行列表可选择的银行都支持。请认真填写银行卡信息（银行卡号、持卡人姓名、银行名称）。不正确的信息会造成提现失败。</p>
                    <h3>Q5:提现申请成功后，多久可以到账？</h3>
                    <p>由于收款银行不同，到账速度也会不同，一般提现到账时间为3-5个工作日。</p>
                    <h3>Q6为什么我的提现会失败？</h3>
                    <p>提现失败的原因可能有：银行卡信息填写错误；账户存在风险被饿了么风控拦截；超出当日提现规则。</p>
                </div>
            </div>
        )
    }
}
export default Balance;



