import React,{Component} from 'react';
// import {Link} from 'react-router-dom';
import MerchantLi from './merchantLi';

class Merchant extends Component{
    constructor(){
        super();
        this.state = {
            onoff:false
        }
    }
    unfold =()=>{
        let {onoff} = this.state;
        this.setState({
            onoff:!onoff
        })
    }
    render(){
        let {
            name,
            avatar,
            description,
            deliveryTime,
            score,
            percentage,
            sellCount,
            minPrice,
            ratingCount,
            deliveryPrice,
            distance,
            num,
            supports
        } = this.props;
        let star = percentage+'%';
        let {onoff} = this.state;
        let open = onoff?'zDis':'zDis zHid';
        let rotate = onoff?'zDown zUp':'zDown';
        //console.log(supports)
        let supportsLi = supports.map((e,i)=>{
            let arr = ['activity_1','activity_2','activity_3','activity_4'];
            let data = {
                key:i,
                type:e.type,
                description:e.description,
                open:i<2?'zDis':open,
                background:arr[Math.round(Math.random()*(arr.length-1))]
            }
            //console.log(data.background)
            return <MerchantLi {...data}/>
        })
        return(
            <div>
                <li>
                    <div className="li_left">
                        <div className="shop_log">
                            <img src={avatar} alt="商户"/>
                        </div>
                    </div>
                    <div className="li_right">
                        <div className="zItem">
                            <div className="username">
                                <span>{name}</span>
                            </div>
                            <div className="bill">
                                <span>保</span>
                                <span>票</span>
                            </div>
                        </div>
                        <div className="zItem zItem_mt">
                            <div className="shop_star">
                                <div className="star">
                                    <div className="gray">
                                        <i className="icon-star"></i>
                                        <i className="icon-star"></i>
                                        <i className="icon-star"></i>
                                        <i className="icon-star"></i>
                                        <i className="icon-star"></i>
                                    </div>
                                    <div className="yellow" style={{width:star}}>
                                        <i className="icon-star"></i>
                                        <i className="icon-star"></i>
                                        <i className="icon-star"></i>
                                        <i className="icon-star"></i>
                                        <i className="icon-star"></i>
                                    </div>
                                </div>
                                <span className="grade">{score}</span>
                                <span>月售{sellCount}单</span>
                            </div>
                            <div className="distribution">
                                <span>{description}</span>
                            </div>
                        </div>
                        <div className="zItem zItem_mt">
                            <div className="delivery">
                                <span>¥{minPrice}起送</span>
                                <i>/</i>
                                <span>配送费¥{deliveryPrice}</span>
                                <i>/</i>
                                <span>¥{ratingCount}/人</span>
                            </div>
                            <div className="zTime">
                                <span>{distance}km</span>
                                <i>/</i>
                                <span className="blue">{deliveryTime}分钟</span>
                            </div>
                        </div>
                        <div className="discount">
                            <div className="zList">
                                {supportsLi}
                            </div>
                            <div className="count"
                                onClick={this.unfold}
                            >
                                <span>{num}个活动</span>
                                <span className={rotate}>
                                    <i className="icon-triangle"></i>
                                </span>
                            </div>
                        </div>
                    </div>
                </li>
            </div>
        )
    }
}
export default Merchant;



