import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './discountsStyle.css';

class Discounts extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('我的红包')
        return(
            <div>
                <div className="Header">
                    <div className="discountsTxT">
                        <Link to='/user' className="icon-direction_l"></Link>
                        <div>我的红包</div>
                    </div>
                </div>
                <section className="hongbao">
                    <p>您的红包0个</p>
                </section>
                <section className="no_dis">
                    <img src="https://i.loli.net/2017/09/02/59aabde208f25.gif" alt="红包"/>
                    <h3>登录后查看红包信息</h3>
                    <button>立即登录</button>
                </section>
                <ul className="redPack">
                    <li className="redPack_li">
                        <div className="redPack_l">
                            <p className="redPack_l_t">
                                <span className="rmb">¥</span>
                                <span className="rmb_l">5</span>
                                <span className="rmb_r">.0</span>
                            </p>
                            <p className="redPack_l_b">满30元可用</p>
                        </div>
                        <div className="redPack_m">
                            <p className="share">分享红包</p>
                            <p className="expire">2017-09-10到期</p>
                            <p className="expire">限收货手机号138****7714</p>
                        </div>
                        <div className="redPack_r">
                            <p>剩2日</p>
                            <button className="employ">立即使用</button>
                        </div>
                    </li>
                    <li className="redPack_li">
                        <div className="redPack_l">
                            <p className="redPack_l_t">
                                <span className="rmb">¥</span>
                                <span className="rmb_l">5</span>
                                <span className="rmb_r">.0</span>
                            </p>
                            <p className="redPack_l_b">满30元可用</p>
                        </div>
                        <div className="redPack_m">
                            <p className="share">分享红包</p>
                            <p className="expire">2017-09-10到期</p>
                            <p className="expire">限收货手机号138****7714</p>
                        </div>
                        <div className="redPack_r">
                            <p>剩2日</p>
                            <button className="employ">立即使用</button>
                        </div>
                    </li>
                </ul>
            </div>
        )
    }
}
export default Discounts;



