import React,{Component} from 'react';
import ReactDOM from 'react-dom';
import {
BrowserRouter as Router,
Route,
Switch
} from 'react-router-dom';
import './css/Pixel Ratio.js';
import './css/reset.css';
import './css/icomoon.css';
import Home from './components/home/home';
import Discover from './components/discover/discover';
import Order from './components/order/order';
import User from './components/user/user';
import Location from './components/home/location';
import Search from './components/home/search';
import Login from './components/user/login';
import Info from './components/user/info';
import Points from './components/user/points';
import Discounts from './components/user/discounts';
import Shopping from './components/user/shopping';
import Service from './components/user/service'
import Download from './components/user/download';
import Ontime from './components/user/question/ontime';
import Payment from './components/user/question/payment';
import Balance from './components/user/question/balance';
import Bonuses from './components/user/question/bonuses';
import Integral from './components/user/question/integral';
import Member from './components/user/question/member';
import Activity from './components/user/question/activity';
import Other from './components/user/question/other';

