class App extends Component{
    render(){
        // userData.forEach((e,i)=>{
        //     localStorage.setItem('user',JSON.stringify(userData[0]))
        // })
        return(
            <Router>
                <div>
                    <Switch>
                        <Route exact path="/" component={Home}/>
                        <Route exact path="/search" component={Search}/>
                        <Route exact path="/location" component={Location}/>
                        <Route exact path="/discover" component={Discover}/>
                        <Route exact path="/order" component={Order} />
                        <Route exact path="/user" component={User} />
                        <Route exact path="/login" component={Login}/>
                        <Route exact path="/user/info" component={Info}/>
                        <Route exact path="/user/points" component={Points}/>
                        <Route exact path="/user/discounts" component={Discounts}/>
                        <Route exact path="/user/shopping" component={Shopping}/>
                        <Route exact path="/user/service" component={Service} />
                        <Route exact path="/user/download" component={Download} />
                        <Route exact path="/user/service/ontime" component={Ontime}/>
                        <Route exact path="/user/service/payment" component={Payment}/>
                        <Route exact path="/user/service/balance" component={Balance} />
                        <Route exact path="/user/service/bonuses" component={Bonuses} />
                        <Route exact path="/user/service/integral" component={Integral}/>
                        <Route exact path="/user/service/member" component={Member}/>
                        <Route exact path="/user/service/activity" component={Activity} />
                        <Route exact path="/user/service/other" component={Other} />
                    </Switch>
                </div>
            </Router>  
        )
    }
}
ReactDOM.render(<App/>
, document.getElementById('root'));

if(module.hot){
    module.hot.accept();
}



