import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './questionStyle.css';

class Integral extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('积分问题')
        return(
            <div>
                <div className="Header">
                    <div className="timeTxT">
                        <Link className="icon-direction_l" to='/user/service'></Link>
                        <div>积分问题</div>
                    </div>
                </div>
                <div className="question">
                    <h3>Q1: 怎么获得积分？</h3>
                    <p>在线支付的订单将获得订单积分奖励：</p>
                    <ul>
                        <li>积分将在用户完成评价后获得，会根据订单实际支付金额和评价的质量发放。</li>
                        <li>每位用户每天最多可以获得2000积分，体验商家的订单和评价不会增加积分。</li>
                    </ul>
                    <h3>Q2: 积分用来做什么？</h3>
                    <p>可以在积分商城兑换各种礼品。</p>
                    <h3>Q3: 关于积分商城兑换礼品发货时间及配送范围？</h3>
                    <ul>
                        <li>为确保您能正常收到礼品，兑换/中奖后请在24小时内填写正确并可正常通信的手机号及地址,逾期视为自动放弃；</li>
                        <li>兑换时请仔细核对收货信息，商品一经兑换，不支持收货地址和（或）收件人信息修改；</li>
                        <li>全额兑换实物类商品将在兑换后15个工作日内安排发货（由于自然灾害等不可抗力因素除外）；活动中获取的实物类奖品将在活动结束后15个工作日内安排发货（由于自然灾害等不可抗力因素除外）；</li>
                        <li>活动奖品/全额兑换商品的配送规则：快递包邮（海外，港澳台，西藏、新疆等偏远地区由于物流原因暂不支持）；</li>
                        <li>饿了么保留法律范围内允许的对活动的解释权。</li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default Integral;



