import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './questionStyle.css';

class Activity extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('活动问题')
        return(
            <div>
                <div className="Header">
                    <div className="timeTxT">
                        <Link className="icon-direction_l" to='/user/service'></Link>
                        <div>活动问题</div>
                    </div>
                </div>
                <div className="question">
                    <h3>Q1: 我是新用户，为什么不能享受新用户下单立减优惠／新用户首单红包？</h3>
                    <ul>
                        <li>新用户是指您的下单设备、手机号、饿了么账号都是第一次在饿了么平台使用。以上任意条件不满足，都不能享受新用户优惠。</li>
                        <li>新用户立减优惠、新用户红包、满减特价菜活动不能同时使用。在您首次下单过程中，我们会在老用户活动和新用户立减中，优先为您选择优惠最大（优惠金额最高）的。</li>
                    </ul>
                    <h3>Q2: 商品活动规则</h3>
                    <ul>
                        <li>新用户下首单时，如果用户自己主动退单，则下次不可再享受新用户优惠，如果是商家或客服取消订单，则可以再次享受新用户优惠。</li>
                        <li>7.5版本（不含）之前的所有版本，用户在下单时不再享受任何优惠（包含新用户、在线满减、商品折扣、红包等）。</li>
                        <li>移动端所有活动，相同手机号或设备每天只限3单，超过3单收取原价。</li>
                        <li>每个订单最多享受3份活动优惠，超过3份的其余商品收取原价。</li>
                        <li>下单手机号和所选择下单的商家不在同一城市，不享受优惠活动。</li>
                        <li>配送费不计入满减活动的门槛。</li>
                    </ul>
                    <h3>Q3: 我第一次在 "A" 商家下单，"A" 商家有参加「新用户」活动，我为什么享受不到新用户的立减优惠？</h3>
                    <ul>
                        <li>活动中的新用户指的是饿了么网站的新用户，而不是指 "A" 商家的新客户。如果您不符合饿了么网站新用户的定义则无法享受于新用户相关的活动优惠。</li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default Activity;



