import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './loginStyle.css';

class Login extends Component{
    constructor(){
        super();
        this.state = {
            username:'',
            password:'',
            onoff:false,
            mask:false,
            val:'',
            succeed:false
        }
    }
    ChangeUser =(ev)=>{
        this.setState({
            username:ev.target.value
        })
    }
    ChangePass =(ev)=>{
        this.setState({
            password:ev.target.value
        })
    }
    click =()=>{
        let {onoff} = this.state;
        this.setState({
            onoff:!onoff
        })
    }
    userLogin =()=>{
        let {username,password} = this.state;
        let {mask} = this.state;
        if(username===''&&password===''){
            this.setState({
                mask:!mask,
                val:'请输入手机/邮箱/用户名及密码'
            })
        }else if(username===''&&password!==''){
            this.setState({
                mask:!mask,
                val:'请输入手机/邮箱/用户名'
            })
        }else if(username!==''&&password===''){
            this.setState({
                mask:!mask,
                val:'请输入密码'
            })
        }else if(username==='admin'&&password==='123456'){
            this.setState({
                mask:!mask,
                val:'登录成功',
                succeed:true
            })
        }else{
            this.setState({
                mask:!mask,
                val:'手机/邮箱/用户名及密码不正确'
            })
        }
    }
    maskDel =()=>{
        this.setState({
            mask:false
        })
    }
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('登录')
        //console.log(this.state.username,this.state.password)
        let {onoff} = this.state;
        let Sclass = onoff?'bgRight':'bgLeft';
        let Sclass2 = onoff?'circleR':'circleL';
        let type = onoff?'text':'password';
        let text = onoff?'ON':'OFF';
        //console.log(this.state.onoff)
        let {mask} = this.state;
        let Con = mask?'mask maskShow':'mask';
        let {val} = this.state;
        let {succeed} = this.state;
        let loginOk = null;
        let {username,password} = this.state;
        let info = {nsername:username,password:password,succeed:succeed};
        if(succeed){
            loginOk = (
                <Link className="maskDel"
                to='/user'
                onClick={this.maskDel}
                >确认</Link>
            )
            localStorage.setItem('user',JSON.stringify(info));
            console.log(localStorage.getItem('user'))
        }else{
            loginOk = (
                <div className="maskDel"
                onClick={this.maskDel}
                >确认</div>
            )
        }
        return(
            <div>
                <div className="Header">
                    <div className="loginTxT">
                        <Link to='/user' className="icon-direction_l"></Link>
                        <div>登录</div>
                    </div>
		        </div>
                <section className="loginUser">
                    <img src="https://i.loli.net/2017/08/29/59a56977ac971.png" alt="饿了么"/>
                    <div className="loginItem">
                        <input type="text" className="inps" placeholder="手机/邮箱/用户名"
                            //value={this.state.username}
                            onChange={this.ChangeUser}
                        />
                    </div>
                    <div className="loginItem">
                        <input type={type} className="inps" placeholder="密码"
                            //value={this.state.password}
                            onChange={this.ChangePass}
                        />
                        <div className="pass"
                            onClick={this.click}
                        >
                            <div className={Sclass}>{text}</div>
                            <div className={Sclass2}></div>
                        </div>
                    </div>
                    <div className="hint">
                        <p>温馨提示：未注册饿了么帐号的手机号，登录时将自动注册，且代表您已同意<a href="//h5.ele.me/service/agreement/">《用户服务协议》</a></p>
                    </div>
                    <button onClick={this.userLogin}>登录</button>
                </section>
                <div className={Con}>
                    <div className="maskCon">
                        <div className="circle">
                            <span className="circleS1"></span>
                            <span className="circleS2"></span>
                        </div>
                        <p className="tips">{val}</p>
                        {/* <div className="maskDel"
                            onClick={this.maskDel}
                        >确认</div> */}
                        {loginOk}
                    </div>
                </div>
            </div>
        )
    }
}
export default Login;



