import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './pointsStyle.css';

class Points extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('我的积分')
        return(
            <div>
                <div className="Header">
                    <div className="pointsTxT">
                        <Link to='/user' className="icon-direction_l"></Link>
                        <div>我的积分</div>
                    </div>
                </div>
                <section className="card">
                    <section className="cardCon">
                        <div className="cardHead">
                            <span>当前积分</span>
                            <a className="cardA">积分说明</a>
                        </div>
                        <p className="cardSum">
                            <span className="cardS1">0</span>
                            <span className="cardS2">分</span>
                        </p>
                        <button>积分兑换商品</button>
                    </section>
                </section>
                <section className="record">
                    <p>最近30天积分记录</p>
                </section>
                <section className="no_points">
                    <img src="https://i.loli.net/2017/09/02/59aabde1e6939.gif" alt="积分"/>
                    <h3>您还没有登录</h3>
                    <p>快去登录赚取大量积分吧</p>
                </section>
                <ul className="pointsList">
                    <li className="pointsLi">
                        <div className="points_L">
                            <span className="order_points">订单积分</span>
                            <span className="pointsTime">2017-09-01</span>
                        </div>
                        <span className="points_R">+100</span>
                    </li>
                    <li className="pointsLi">
                        <div className="points_L">
                            <span className="order_points">订单积分</span>
                            <span className="pointsTime">2017-09-01</span>
                        </div>
                        <span className="points_R">+100</span>
                    </li>
                    <li className="pointsLi">
                        <div className="points_L">
                            <span className="order_points">订单积分</span>
                            <span className="pointsTime">2017-09-01</span>
                        </div>
                        <span className="points_R">+100</span>
                    </li>
                </ul>
            </div>
        )
    }
}
export default Points;



