import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './serviceStyle.css';

class Service extends Component{
    render(){
        $('body').css('background','#fff')
        $('title').html('服务中心')
        return(
            <div>
                <div className="Header">
                    <div className="serviceTxt">
                        <Link to='/user' className="icon-direction_l"></Link>
                        <div>服务中心</div>
                    </div>
                </div>
                <section className="Problem">
                    <h3>热门问题</h3>
                    <ul>
                        <li>
                            <Link className="item_a" to='/user/service/ontime'>
                                <span>准时达问题</span>
                                <i className="icon-direction_r"></i>
                            </Link>
                        </li>
                        <li>
                            <Link className="item_a" to='/user/service/payment'>
                                <span>支付问题</span>
                                <i className="icon-direction_r"></i>
                            </Link>
                        </li>
                        <li>
                            <Link className="item_a" to='/user/service/balance'>
                                <span>余额问题</span>
                                <i className="icon-direction_r"></i>
                            </Link>
                        </li>
                        <li>
                            <Link className="item_a" to='/user/service/bonuses'>
                                <span>红包问题</span>
                                <i className="icon-direction_r"></i>
                            </Link>
                        </li>
                        <li>
                            <Link className="item_a" to='/user/service/integral'>
                                <span>积分问题</span>
                                <i className="icon-direction_r"></i>
                            </Link>
                        </li>
                        <li>
                            <Link className="item_a" to='/user/service/member'>
                                <span>会员问题</span>
                                <i className="icon-direction_r"></i>
                            </Link>
                        </li>
                        <li>
                            <Link className="item_a" to='/user/service/activity'>
                                <span>活动问题</span>
                                <i className="icon-direction_r"></i>
                            </Link>
                        </li>
                        <li>
                            <Link className="item_a" to='/user/service/other'>
                                <span>其它问题</span>
                                <i className="icon-direction_r"></i>
                            </Link>
                        </li>
                    </ul>
                </section>
            </div>
        )
    }
}
export default Service;



