import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './questionStyle.css';

class Other extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('其它问题')
        return(
            <div>
                <div className="Header">
                    <div className="timeTxT">
                        <Link className="icon-direction_l" to='/user/service'></Link>
                        <div>其它问题</div>
                    </div>
                </div>
                <div className="question">
                    <h3>Q1: 什么是订单数？</h3>
                    <p>订单数是指商家最近30天的订单总数，这个数据是动态的，每天都会进行更新。</p>
                    <h3>Q2: 商品多久内可以评价？评价后是否能修改？</h3>
                    <ul>
                        <li>下单确认收货后，7天内可以对商品进行评价。超过7天，评价功能自动失效。</li>
                        <li>美食一旦点评就不能做修改，评价不可删除撤销，请知悉。</li>
                    </ul>
                    <h3>Q3: 为什么提示下单次数过多，无法下单？</h3>
                    <p>在24小时内，同一手机号在同一设备上最多可以成功提交7次订单。</p>
                </div>
            </div>
        )
    }
}
export default Other;



