import React,{Component} from 'react';
import './fouter.css';
import FooterList from './footerList';

class Footer extends Component{
    constructor(){
        super();
        this.state = {
            list:[
                {name:'外卖',classChild:'icon-ele icon',hash:'/'},
                {name:'发现',classChild:'icon-circum icon',hash:'/discover'},
                {name:'订单',classChild:'icon-order2 icon',hash:'/order'},
                {name:'我的',classChild:'icon-user icon',hash:'/user'}
            ]
        }
    }
    render(){
        let {list} = this.state;
        let foolist = null;
        foolist = list.map((e,i)=>{
            let data = {
                key:i,
                name:e.name,
                classChild:e.classChild,
                hash:e.hash
            }
            return <FooterList {...data}/>
        })
        return(
            <div>
                <div className="zFooter">
                    <div className="zAction">
                        {/* <Link to='/' className="zAction_a">
                            <i className="icon-ele icon"></i>
                            <span>外卖</span>
                        </Link>
                        <Link to='/discover' className="zAction_a">
                            <i className="icon-circum icon"></i>
                            <span>发现</span>
                        </Link>
                        <Link to='/order' className="zAction_a">
                            <i className="icon-order2 icon"></i>
                            <span>订单</span>
                        </Link>
                        <Link to='/user' className="zAction_a">
                            <i className="icon-user icon"></i>
                            <span>我的</span>
                        </Link> */}
                        {foolist}
                    </div>
                </div>
            </div>
        )
    }
}
export default Footer;



